const ajax = new XMLHttpRequest();
// ajaxÎäî  new XMLHttpRequestÍ∞Ä Î∞òÌôòÌïòÎäî Í∞íÏùÑ Ï†ÄÏû•ÌïòÎäî Î≥ÄÏàò

const container = document.getElementById('root');
const content = document.createElement('div');

const NEWS_URL = 'https://api.hnpwa.com/v0/news/1.json';
const CONTENT_URL = 'https://api.hnpwa.com/v0/item/@id.json';

ajax.open('GET', NEWS_URL, false);
// openÏùò Î©îÏÜåÎìúÎäî Ï≤´Î≤àÏß∏ string, ÎëêÎ≤àÏß∏Îäî url Î¨∏ÏûêÏó¥ ÏÑ∏Î≤àÏß∏Îäî async: boolean (false: ÎèôÍ∏∞Î°ú Í∞ÄÏ†∏Ïò¥)

ajax.send();
// Îç∞Ïù¥ÌÑ∞Î•º Í∞ÄÏ†∏Ïò¥

// ÏùëÎãµÍ∞íÏùÑ jsonÌòïÌÉúÏù∏ Í∞ùÏ≤¥Î°ú Î≥ÄÌôò
const newsFeed = JSON.parse(ajax.response);
console.log(newsFeed);

// Ï†ïÏ†ÅÏúºÎ°ú ÌÉúÍ∑∏ Ï∂îÍ∞Ä
// document.getElementById('root').innerHTML = `
// <ul>
//   <li>${newsFeed[0].title}</li>
//   <li>${newsFeed[1].title}</li>
//   <li>${newsFeed[2].title}</li>
// </ul>
// `

// ÎèôÏ†ÅÏúºÎ°ú ÌÉúÍ∑∏ Ï∂îÍ∞Ä
const ul = document.createElement('ul');

window.addEventListener('hashchange', function() {
  const id = location.hash.substr(1);

  console.log(id, CONTENT_URL.replace('@id', id))

  ajax.open('GET', CONTENT_URL.replace('@id', id), false);
  ajax.send();

  const newsContent = JSON.parse(ajax.response);
  console.log('newsContent :', newsContent)

  const title = document.createElement('h1');

  title.innerHTML = newsContent.title;
  content.appendChild(title);
});

for(let i = 0; i < newsFeed.length; i++) {
  const li = document.createElement('li');
  const a = document.createElement('a');
 
  a.href = `#${newsFeed[i].id}`;
  a.innerHTML = `${newsFeed[i].title} (${newsFeed[i].comments_count})`;

  li.appendChild(a);
  ul.appendChild(li);
}

container.appendChild(ul);
container.appendChild(content);